import React from 'react';

const Icon = (props) => {
  const { number, href, alt, src } = props;
  return (
    <span className={`icon ${number}`}>
      <a href={`${href}`}>
        <img src={`${src}`} alt={`${alt}`} height='50px' width="50px" />
      </a>
    </span>
  );
};

export default Icon;
